import videojs from 'video.js';

const Component = videojs.getComponent('Component');

class SkipButtons extends Component {
    createEl() {
        const el = videojs.dom.createEl('div', {
            className: 'vjs-skip-buttons',
            innerHTML: `
                    <div class="vjs-icon-replay-5 pr-10 cursor-pointer">
                            Replay
                    </div>
                    <div class="vjs-icon-forward-5 cursor-pointer">
                            Forward
                    </div>
                `
        });

        return el;
    }
}

videojs.registerComponent('SkipButtons', SkipButtons);

export default SkipButtons;
